<template>
  <div class="web" :class="{index:index}">
    <div class="navbar" v-if="!index">
      <div class="sidebar-button" @click="expdown=!expdown">
        <svg xmlns="http://www.w3.org/2000/svg" aria-hidden="true" role="img" viewBox="0 0 448 512" class="icon">
          <path fill="currentColor"
                d="M436 124H12c-6.627 0-12-5.373-12-12V80c0-6.627 5.373-12 12-12h424c6.627 0 12 5.373 12 12v32c0 6.627-5.373 12-12 12zm0 160H12c-6.627 0-12-5.373-12-12v-32c0-6.627 5.373-12 12-12h424c6.627 0 12 5.373 12 12v32c0 6.627-5.373 12-12 12zm0 160H12c-6.627 0-12-5.373-12-12v-32c0-6.627 5.373-12 12-12h424c6.627 0 12 5.373 12 12v32c0 6.627-5.373 12-12 12z"></path>
        </svg>
      </div>
      <router-link to="/" tag="a" class="home-link"></router-link>
      <div class="searcharea">
        <div class="search-box">
          <input autocomplete="off" v-model="keyword">
          <ul class="suggestions" v-if="suggestList.length>0">
            <li v-for="(item,xh) in suggestList" :key="xh">
              <router-link :to="'/'+currentPath+'/'+item.id" tag="a">{{xh+1}}、{{item.title}}</router-link>
            </li>
          </ul>
        </div>
        <nav class="nav-links" v-show="expdown">
          <div class="nav-item">
            <router-link to="/articles" tag="a">文章</router-link>
          </div>
          <div class="nav-item">
            <router-link to="/techs" tag="a">前端</router-link>
          </div>
          <div class="nav-item">
            <router-link to="/works" tag="a">作品</router-link>
          </div>
          <div class="nav-item">
            <router-link to="/lifes" tag="a">生活</router-link>
          </div>
          <div class="nav-item">
            <router-link to="/mottos" tag="a">格言</router-link>
          </div>
          <div class="nav-item">
            <router-link to="/about" tag="a">关于</router-link>
          </div>
        </nav>
      </div>
    </div>
    <header v-if="index">
      <div class="logo">
        <router-link to="/" tag="a"></router-link>
      </div>
      <nav>
        <ul>
          <router-link to="/articles" tag="li">文章</router-link>
          <router-link to="/techs" tag="li">前端</router-link>
          <router-link to="/works" tag="li">作品</router-link>
          <router-link to="/lifes" tag="li">生活</router-link>
          <router-link to="/mottos" tag="li">格言</router-link>
          <router-link to="/about" tag="li">关于</router-link>
        </ul>
      </nav>
    </header>
    <router-view :expleft="expleft" :expup.sync="expup"></router-view>
    <ul class="rightNav">
      <li class="iconfont icon-menu pc" v-if="detail" @click="navleft"></li>
      <li class="iconfont icon-menu mobile" v-if="detail" @click="navup"></li>
      <li class="iconfont icon-l-goToTop" @click="totop"></li>
    </ul>
    <ad></ad>
  </div>
</template>
<script>
import ad from '@/views/ad'
import '@/assets/css/lee.css'
import '@/assets/css/lee-mb.css'
import MiniSearch from 'minisearch'

export default {
    name: 'myweb',
    components: {
        ad
    },
    data() {
        return {
            currentPath: '',//用来连接地址
            keyword: '',
            suggestList: [],//搜索列表
            expdown: true,//这个是移动端菜单是否显示
            index: false,
            detail: false,//是不是详细页
            expleft: true,//详细页PC端那个导航关闭与否
            expup: false,//详细页移动端那个导航关闭与否
        }
    },
    mounted() {
        let sw = window.innerWidth
        let _this = this
        cl(sw)
        window.addEventListener('resize', () => {
            sw = window.innerWidth
            cl(sw)
        })

        function cl(sw) {
            if (sw > 768) {
                _this.expleft = true
                _this.expup = false
                _this.expdown = true
            } else {
                _this.expleft = false
                _this.expup = false
                _this.expdown = false
            }
        }

        //统计代码
        var _hmt = _hmt || [];
        (function () {
            var hm = document.createElement("script");
            hm.src = "https://hm.baidu.com/hm.js?3384d95e63352dd4d880e5b0aa3b3f9a";
            var s = document.getElementsByTagName("script")[0];
            s.parentNode.insertBefore(hm, s);
        })();


    },
    methods: {
        totop() {
            document.documentElement.scrollIntoView({behavior: "smooth", block: "start"});
        },
        navleft() {
            this.expleft = !this.expleft
        },
        navup() {
            this.expup = !this.expup
        }
    },
    computed: {},
    watch: {
        keyword: {
            immediate: true,
            handler(value) {
                //console.log(value, this.$route.path)
                let apiUrl
                if (this.$route.path.indexOf('article') > -1) {
                    apiUrl = 'article'
                    this.currentPath = 'articles'
                } else if (this.$route.path.indexOf('tech') > -1) {
                    apiUrl = 'techs'
                    this.currentPath = 'techs'
                } else if (this.$route.path.indexOf('life') > -1) {
                    apiUrl = 'life'
                    this.currentPath = 'lifes'
                } else {
                    apiUrl = 'article'
                    this.currentPath = 'articles'
                }
                fetch('https://www.fastmock.site/mock/5f854ec44ed74b246903c6e75806ec14/api/' + apiUrl).then((rs) => {
                    return rs.json()
                }).then((data) => {
                    let miniSearch = new MiniSearch({
                        fields: ['title'],
                        storeFields: ['title'],
                        searchOptions: {
                            fuzzy: 16
                        }
                    })
                    miniSearch.addAll(data)
                    value=value.toLowerCase()
                    let result = miniSearch.search(value, {
                        filter: (result) => result.title.toLowerCase().indexOf(value) > -1
                    })
                    this.suggestList = result
                })

            }
        },
        $route(to) {
            this.expup = false
            this.keyword = ''
            this.suggestList = []
            let sw = window.innerWidth
            if (sw > 768) {
                this.expdown = true
            } else {
                this.expdown = false
            }
            this.expleft = true
            if (to.fullPath === '/') {
                this.index = true
            } else {
                this.index = false
            }
            if (to.meta.detail) {
                this.detail = true
            } else {
                this.detail = false
            }
        }
    }
}
</script>
