<template>
  <div class="newyearad">
    <div class="mark"></div>
    <div class="win">
      <i class="close">x</i>
      <div class="nr">
        <h2>兔年身体健康</h2>
        <img src="https://s1.ax1x.com/2022/12/30/pSpBJKA.png">
        <div class="text">
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
    name: 'ad',
    data() {
        return {}
    },
    mounted() {
        let newyearad = document.querySelector('.newyearad')
        let close = newyearad.querySelector('.close')
        close.addEventListener('click', () => {
            newyearad.style.display = 'none'
        });
        if (getCookie("newYear")) {
            newyearad.style.display = 'none'
        } else {
            newyearad.style.display = 'block'
            setCookie("newYear", "newYear", 1);
        }

        function setCookie(cname, cvalue, exdays) {
            let d = new Date();
            d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
            let expires = "expires=" + d.toUTCString();
            document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
        }

        function getCookie(cname) {
            let name = cname + "=";
            let allCookie = document.cookie
            let ca = allCookie.split(';')
            let el, result;
            ca.map(function (elem) {
                el = elem.replace(/\s+/g, "");
                if (el.indexOf(name) == 0) {
                    result = el.substring(name.length, el.length);
                }
            })
            return result
        }


    },
    methods: {},
    computed: {}
}
</script>
